import React, { createContext, useState, useEffect } from 'react';
import axios from 'axios';
import { baseURL } from './../components/config';

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
    const [user, setUser] = useState(null);

    useEffect(() => {
        const token = localStorage.getItem('token');
        if (token) {
            axios.get(`${baseURL}/profiles/api/profile/`, {
                headers: { Authorization: `Token ${token}` }
            })
            .then(response => {
                console.log("Profile fetched successfully:", response.data);
                setUser(response.data);
            })
            .catch((error) => {
                console.log("Failed to fetch profile:", error);
                localStorage.removeItem('token');
            });
        }
    }, []);

    const login = async (username, password) => {
        console.log("Attempting login with:", { username, password });
        try {
            const response = await axios.post(`${baseURL}/profiles/api/login/`, { username, password });
            console.log("Login response:", response.data);
            localStorage.setItem('token', response.data.token);
            setUser({
                id: response.data.user_id,
                email: response.data.email,
                role: response.data.role,
            });
        } catch (error) {
            console.log("Login failed:", error);
            throw error;
        }
    };

    const logout = () => {
        console.log("Logging out");
        localStorage.removeItem('token');
        setUser(null);
    };

    const register = async (username, email, password, role) => {
        try {
            console.log("Attempting registration with:", { username, email, password, role });
            const response = await axios.post(`${baseURL}/profiles/api/register/`, { username, email, password, role });
            console.log("Registration successful:", response.data);
            return response.data;
        } catch (error) {
            console.log("Registration failed:", error);
            throw error;
        }
    };
    const updateProfile = async (profileData) => {
        const token = localStorage.getItem('token');
        console.log("Updating profile with:", profileData);
        try {
            const response = await axios.put(`${baseURL}/profiles/api/profile/`, profileData, {
                headers: { Authorization: `Token ${token}` }
            });
            console.log("Profile updated:", response.data);
            setUser(response.data);
        } catch (error) {
            console.log("Profile update failed:", error);
            throw error;
        }
    };

    return (
        <AuthContext.Provider value={{ user, login, logout, register, updateProfile }}>
            {children}
        </AuthContext.Provider>
    );
};

export default AuthContext;
