import React, { useState } from 'react';
import ManageCars from './ManageCars';
import ManageDrivers from './ManageDrivers';
import ManageOwners from './ManageOwners';
import PreviousTrips from './PreviousTrips';
import 'bootstrap/dist/css/bootstrap.min.css';

const AdminDashboard = () => {
    const [activeTab, setActiveTab] = useState('cars');

    const renderTabContent = () => {
        switch (activeTab) {
            case 'cars':
                return <ManageCars />;
            case 'trips':
                return <PreviousTrips />;
            case 'drivers':
                return <ManageDrivers />;
            case 'owners':
                return <ManageOwners />;
            default:
                return <ManageCars />;
        }
    };

    return (
        <div className="container mt-5">
            <div className="card shadow-sm p-4">
                <h2 className="card-title mb-4">Admin Dashboard</h2>
                <ul className="nav nav-tabs">
                    <li className="nav-item">
                        <button 
                            className={`nav-link ${activeTab === 'cars' ? 'active' : ''}`} 
                            onClick={() => setActiveTab('cars')}
                        >
                            Cars
                        </button>
                    </li>
                    <li className="nav-item">
                        <button 
                            className={`nav-link ${activeTab === 'trips' ? 'active' : ''}`} 
                            onClick={() => setActiveTab('trips')}
                        >
                            Trips
                        </button>
                    </li>
                    <li className="nav-item">
                        <button 
                            className={`nav-link ${activeTab === 'drivers' ? 'active' : ''}`} 
                            onClick={() => setActiveTab('drivers')}
                        >
                            Drivers
                        </button>
                    </li>
                    <li className="nav-item">
                        <button 
                            className={`nav-link ${activeTab === 'owners' ? 'active' : ''}`} 
                            onClick={() => setActiveTab('owners')}
                        >
                            Car Owners
                        </button>
                    </li>
                </ul>
                <div className="tab-content mt-4">
                    {renderTabContent()}
                </div>
            </div>
        </div>
    );
};

export default AdminDashboard;
