import React, { useState, useContext } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import AuthContext from '../context/AuthContext';
import 'bootstrap/dist/css/bootstrap.min.css';

const Register = () => {
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [role, setRole] = useState('driver');  // Default role
    const [error, setError] = useState(null);
    const { register } = useContext(AuthContext);
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        console.log("Submitting registration form with:", { username, email, password, role });
        try {
            await register(username, email, password, role);
            console.log("Registration successful, navigating to login.");
            navigate('/login');
        } catch (err) {
            console.log("Registration failed:", err.response?.data || err);
            setError(err.response?.data || 'Registration failed');
        }
    };

    return (
        <div className="container mt-5">
            <div className="card shadow-sm p-4">
                <h2 className="card-title mb-4">Register</h2>
                {error && <div className="alert alert-danger">{JSON.stringify(error)}</div>}
                <form onSubmit={handleSubmit}>
                    <div className="form-group mb-3">
                        <label>Username</label>
                        <input 
                            type="text" 
                            className="form-control" 
                            value={username} 
                            onChange={(e) => setUsername(e.target.value)} 
                            required 
                        />
                    </div>
                    <div className="form-group mb-3">
                        <label>Email</label>
                        <input 
                            type="email" 
                            className="form-control" 
                            value={email} 
                            onChange={(e) => setEmail(e.target.value)} 
                            required 
                        />
                    </div>
                    <div className="form-group mb-3">
                        <label>Password</label>
                        <input 
                            type="password" 
                            className="form-control" 
                            value={password} 
                            onChange={(e) => setPassword(e.target.value)} 
                            required 
                        />
                    </div>
                    <div className="form-group mb-3">
                        <label>Role</label>
                        <select 
                            className="form-control" 
                            value={role} 
                            onChange={(e) => setRole(e.target.value)} 
                            required
                        >
                            <option value="driver">Driver</option>
                            <option value="renter">Renter</option>
                        </select>
                    </div>
                    <button type="submit" className="btn btn-primary mt-3">Register</button>
                </form>
                <p className="mt-3">Already have an account? <Link to="/login">Login</Link></p>
            </div>
        </div>
    );
};

export default Register;
