import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Alert, Button, ListGroup, Container } from 'react-bootstrap';
import { baseURL } from './config';

const ManageOwners = () => {
    const [owners, setOwners] = useState([]);
    const [error, setError] = useState('');

    useEffect(() => {
        const fetchOwners = async () => {
            try {
                const token = localStorage.getItem('token');
                const response = await axios.get(`${baseURL}/profiles/api/car_owner/`, {
                    headers: { Authorization: `Token ${token}` }
                });
                setOwners(response.data.filter(user => user.role === 'owner'));
            } catch (err) {
                setError('Error fetching car owners');
            }
        };

        fetchOwners();
    }, []);

    const handleRemoveOwner = async (ownerId) => {
        try {
            const token = localStorage.getItem('token');
            await axios.delete(`${baseURL}/profiles/api/profile/${ownerId}/`, {
                headers: { Authorization: `Token ${token}` }
            });
            setOwners(owners.filter(owner => owner.id !== ownerId));
        } catch (err) {
            setError('Error removing car owner');
        }
    };

    return (
        <Container className="mt-5">
            <h3>Manage Car Owners</h3>
            {error && <Alert variant="danger">{error}</Alert>}
            <ListGroup>
                {owners.map(owner => (
                    <ListGroup.Item key={owner.id} className="d-flex justify-content-between align-items-center">
                        <div>
                            <strong>{owner.username}</strong> - {owner.email}
                        </div>
                        <Button variant="danger" size="sm" onClick={() => handleRemoveOwner(owner.id)}>
                            Remove
                        </Button>
                    </ListGroup.Item>
                ))}
            </ListGroup>
        </Container>
    );
};

export default ManageOwners;
