import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Modal, Button, Form, Card, ListGroup, Alert } from 'react-bootstrap';
import CarDetails from './CarDetails';
import { baseURL } from './config';

const ManageCars = () => {
    const [cars, setCars] = useState([]);
    const [newCar, setNewCar] = useState({ model: '', licensePlate: '' });
    const [error, setError] = useState('');
    const [showAddCarModal, setShowAddCarModal] = useState(false);
    const [selectedCar, setSelectedCar] = useState(null);

    useEffect(() => {
        const fetchCars = async () => {
            try {
                const token = localStorage.getItem('token');
                const response = await axios.get(`${baseURL}/cars/api/cars/`, {
                    headers: { Authorization: `Token ${token}` }
                });
                setCars(response.data);
            } catch (err) {
                setError('Error fetching cars');
            }
        };

        fetchCars();
    }, []);

    const handleAddCar = async (e) => {
        e.preventDefault();
        try {
            const token = localStorage.getItem('token');
            const response = await axios.post(`${baseURL}/cars/api/cars/`, {
                model: newCar.model,
                license_plate: newCar.licensePlate
            }, {
                headers: { Authorization: `Token ${token}` }
            });
            setCars([...cars, response.data]);
            setNewCar({ model: '', licensePlate: '' });
            setShowAddCarModal(false);
        } catch (err) {
            setError('Error adding car');
        }
    };

    const handleRemoveCar = async (carId) => {
        try {
            const token = localStorage.getItem('token');
            await axios.delete(`${baseURL}/cars/api/cars/${carId}/`, {
                headers: { Authorization: `Token ${token}` }
            });
            setCars(cars.filter((car) => car.car_id !== carId));
        } catch (err) {
            setError('Error removing car');
        }
    };

    const toggleAddCarModal = () => {
        setShowAddCarModal(!showAddCarModal);
    };

    return (
        <div className="container mt-5">
            <Card className="p-4 shadow-sm">
                <h2>Manage Cars</h2>
                {error && <Alert variant="danger">{error}</Alert>}
                <Button variant="primary" onClick={toggleAddCarModal} className="mb-3">
                    Add Car
                </Button>

                <Modal show={showAddCarModal} onHide={toggleAddCarModal}>
                    <Modal.Header closeButton>
                        <Modal.Title>Add Car</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={handleAddCar}>
                            <Form.Group controlId="formCarModel" className="mb-3">
                                <Form.Label>Car Model</Form.Label>
                                <Form.Control
                                    type="text"
                                    value={newCar.model}
                                    onChange={(e) => setNewCar({ ...newCar, model: e.target.value })}
                                    required
                                />
                            </Form.Group>
                            <Form.Group controlId="formCarLicensePlate" className="mb-3">
                                <Form.Label>License Plate</Form.Label>
                                <Form.Control
                                    type="text"
                                    value={newCar.licensePlate}
                                    onChange={(e) => setNewCar({ ...newCar, licensePlate: e.target.value })}
                                    required
                                />
                            </Form.Group>
                            <Button variant="primary" type="submit">
                                Add Car
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>

                <div className="mt-3">
                    <h3>Cars</h3>
                    <ListGroup>
                        {cars.map((car) => (
                            <ListGroup.Item key={car.car_id} className="d-flex justify-content-between align-items-center">
                                <div>
                                    <p className="mb-1"><strong>Model:</strong> {car.model}</p>
                                    <p className="mb-1"><strong>License Plate:</strong> {car.license_plate}</p>
                                </div>
                                <div>
                                    <Button variant="danger" className="me-2" onClick={() => handleRemoveCar(car.car_id)}>
                                        Remove
                                    </Button>
                                    <Button variant="info" onClick={() => setSelectedCar(car)}>
                                        Details
                                    </Button>
                                    {selectedCar && selectedCar.car_id === car.car_id && (
                                        <CarDetails car={selectedCar} />
                                    )}
                                </div>
                            </ListGroup.Item>
                        ))}
                    </ListGroup>
                </div>
            </Card>
        </div>
    );
};

export default ManageCars;
