import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate, useLocation } from 'react-router-dom';
import Camera, { FACING_MODES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import { useGeolocated } from 'react-geolocated';
import 'bootstrap/dist/css/bootstrap.min.css';
import { baseURL } from './config';

const DropOff = () => {
    const location = useLocation();
    const tripId = location.state?.tripId;
    const [dropOffFrontPhoto, setDropOffFrontPhoto] = useState(null);
    const [dropOffRearPhoto, setDropOffRearPhoto] = useState(null);
    const [dropOffLeftPhoto, setDropOffLeftPhoto] = useState(null);
    const [dropOffRightPhoto, setDropOffRightPhoto] = useState(null);
    const [dropOffOdometerPhoto, setDropOffOdometerPhoto] = useState(null);
    const [dropOffOptionalPhoto, setDropOffOptionalPhoto] = useState(null);
    const [dropOffOdometer, setDropOffOdometer] = useState('');
    const [dropOffLocation, setDropOffLocation] = useState('');
    const [dropOffNote, setDropOffNote] = useState('');
    const [currentPhotoType, setCurrentPhotoType] = useState(null);
    const [step, setStep] = useState(1);
    const [facingMode, setFacingMode] = useState(FACING_MODES.ENVIRONMENT);
    const navigate = useNavigate();

    const { coords } = useGeolocated({
        positionOptions: {
            enableHighAccuracy: true,
        },
        userDecisionTimeout: 5000,
    });

    useEffect(() => {
        if (coords) {
            const fetchAddress = async () => {
                try {
                    const response = await axios.get(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${coords.latitude}&lon=${coords.longitude}`);
                    const address = response.data.display_name;
                    setDropOffLocation(address);
                } catch (error) {
                    console.error('Error fetching address:', error);
                }
            };

            fetchAddress();
        }
    }, [coords]);

    const handleFileChange = (setFile, dataUri) => {
        fetch(dataUri)
            .then(res => res.blob())
            .then(blob => {
                const file = new File([blob], `${currentPhotoType}.jpg`, { type: 'image/jpeg' });
                setFile(file);
            });
        setCurrentPhotoType(null);
    };

    const handleTakePhoto = (dataUri) => {
        switch (currentPhotoType) {
            case 'DropOffFrontPhoto':
                handleFileChange(setDropOffFrontPhoto, dataUri);
                break;
            case 'DropOffRearPhoto':
                handleFileChange(setDropOffRearPhoto, dataUri);
                break;
            case 'DropOffLeftPhoto':
                handleFileChange(setDropOffLeftPhoto, dataUri);
                break;
            case 'DropOffRightPhoto':
                handleFileChange(setDropOffRightPhoto, dataUri);
                break;
            case 'DropOffOdometerPhoto':
                handleFileChange(setDropOffOdometerPhoto, dataUri);
                break;
            case 'DropOffOptionalPhoto':
                handleFileChange(setDropOffOptionalPhoto, dataUri);
                break;
            default:
                break;
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('trip_id', tripId);
        formData.append('drop_off_front_photo', dropOffFrontPhoto);
        formData.append('drop_off_rear_photo', dropOffRearPhoto);
        formData.append('drop_off_left_photo', dropOffLeftPhoto);
        formData.append('drop_off_right_photo', dropOffRightPhoto);
        formData.append('drop_off_odometer_photo', dropOffOdometerPhoto);
        formData.append('drop_off_optional_photo', dropOffOptionalPhoto);
        formData.append('drop_off_odometer', dropOffOdometer);
        formData.append('drop_off_location', dropOffLocation);
        formData.append('drop_off_note', dropOffNote);

        try {
            const token = localStorage.getItem('token');
            const response = await axios.post(`${baseURL}/trips/api/drop_off/`, formData, {
                headers: { Authorization: `Token ${token}` },
            });
            console.log('Drop-off completed successfully:', response.data);
            navigate('/dashboard');
        } catch (error) {
            console.error('Error during drop-off:', error.response.data);
        }
    };

    const changeCameraView = () => {
        setFacingMode(prevMode => prevMode === FACING_MODES.ENVIRONMENT ? FACING_MODES.USER : FACING_MODES.ENVIRONMENT);
    };

    return (
        <div className="container mt-5">
            <form onSubmit={handleSubmit} className="card p-4 shadow-sm">
                <h2 className="mb-4">Drop Off Car</h2>
                {step === 1 && (
                    <div>
                        <h3>Take Car Photos</h3>
                        {currentPhotoType && (
                            <div className="mb-3">
                                <Camera
                                    onTakePhoto={(dataUri) => handleTakePhoto(dataUri)}
                                    idealFacingMode={facingMode}
                                    isImageMirror={false}
                                    isFullscreen={false}
                                />
                                <div className="mt-2">
                                    <button className="btn btn-secondary" onClick={() => setCurrentPhotoType(null)}>Cancel</button>
                                </div>
                            </div>
                        )}
                        {!currentPhotoType && (
                            <div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('DropOffFrontPhoto')}>
                                        Take Front Photo
                                    </button>
                                    {dropOffFrontPhoto && <img src={URL.createObjectURL(dropOffFrontPhoto)} alt="Front" className="img-thumbnail" width="100" />}
                                </div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('DropOffRearPhoto')}>
                                        Take Rear Photo
                                    </button>
                                    {dropOffRearPhoto && <img src={URL.createObjectURL(dropOffRearPhoto)} alt="Rear" className="img-thumbnail" width="100" />}
                                </div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('DropOffLeftPhoto')}>
                                        Take Left Photo
                                    </button>
                                    {dropOffLeftPhoto && <img src={URL.createObjectURL(dropOffLeftPhoto)} alt="Left" className="img-thumbnail" width="100" />}
                                </div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('DropOffRightPhoto')}>
                                        Take Right Photo
                                    </button>
                                    {dropOffRightPhoto && <img src={URL.createObjectURL(dropOffRightPhoto)} alt="Right" className="img-thumbnail" width="100" />}
                                </div>
                                <button type="button" className="btn btn-primary" onClick={() => setStep(2)}>Next</button>
                            </div>
                        )}
                    </div>
                )}
                {step === 2 && (
                    <div>
                        <h3>Take Odometer Photo</h3>
                        {currentPhotoType && (
                            <div className="mb-3">
                                <Camera
                                    onTakePhoto={(dataUri) => handleTakePhoto(dataUri)}
                                    idealFacingMode={facingMode}
                                    isImageMirror={false}
                                    isFullscreen={false}
                                />
                                <div className="mt-2">
                                    <button className="btn btn-secondary" onClick={() => setCurrentPhotoType(null)}>Cancel</button>
                                </div>
                            </div>
                        )}
                        {!currentPhotoType && (
                            <div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('DropOffOdometerPhoto')}>
                                        Take Odometer Photo
                                    </button>
                                    {dropOffOdometerPhoto && <img src={URL.createObjectURL(dropOffOdometerPhoto)} alt="Odometer" className="img-thumbnail" width="100" />}
                                </div>
                                <button type="button" className="btn btn-primary" onClick={() => setStep(3)}>Next</button>
                            </div>
                        )}
                    </div>
                )}
                {step === 3 && (
                    <div>
                        <h3>Enter Odometer and Location Details</h3>
                        <div className="mb-3">
                            <label className="form-label">Odometer Reading</label>
                            <input type="text" className="form-control" value={dropOffOdometer} onChange={(e) => setDropOffOdometer(e.target.value)} required />
                        </div>
                        <div className="mb-3">
                            <label className="form-label">Drop Off Location</label>
                            <input type="text" className="form-control" value={dropOffLocation} readOnly />
                            {dropOffLocation && (
                                <a
                                    href={`https://www.google.com/maps/search/?api=1&query=${encodeURIComponent(dropOffLocation)}`}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="btn btn-link"
                                >
                                    View on Google Maps
                                </a>
                            )}
                        </div>
                        <button type="button" className="btn btn-primary" onClick={() => setStep(4)}>Next</button>
                    </div>
                )}
                {step === 4 && (
                    <div>
                        <h3>Optional Details</h3>
                        <div className="mb-3">
                            <textarea
                                className="form-control"
                                placeholder="Drop off note"
                                value={dropOffNote}
                                onChange={(e) => setDropOffNote(e.target.value)}
                            ></textarea>
                        </div>
                        {currentPhotoType && (
                            <div className="mb-3">
                                <Camera
                                    onTakePhoto={(dataUri) => handleTakePhoto(dataUri)}
                                    idealFacingMode={facingMode}
                                    isImageMirror={false}
                                    isFullscreen={false}
                                />
                                <div className="mt-2">
                                    <button className="btn btn-secondary" onClick={() => setCurrentPhotoType(null)}>Cancel</button>
                                </div>
                            </div>
                        )}
                        {!currentPhotoType && (
                            <div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('DropOffOptionalPhoto')}>
                                        Take Optional Photo
                                    </button>
                                    {dropOffOptionalPhoto && <img src={URL.createObjectURL(dropOffOptionalPhoto)} alt="Optional" className="img-thumbnail" width="100" />}
                                </div>
                                <button type="submit" className="btn btn-primary">Complete Drop Off</button>
                            </div>
                        )}
                    </div>
                )}
            </form>
        </div>
    );
};

export default DropOff;
