import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate, useLocation } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import { baseURL } from './config';

const CompleteTrip = () => {
    const location = useLocation();
    const tripId = location.state?.tripId;
    const [routeNo, setRouteNo] = useState('');
    const [totalDeliveries, setTotalDeliveries] = useState('');
    const [returnDeliveries, setReturnDeliveries] = useState('');
    const [successfulDeliveries, setSuccessfulDeliveries] = useState('');
    const [note, setNote] = useState('');
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('trip_id', tripId);
        formData.append('route_no', routeNo);
        formData.append('total_deliveries', totalDeliveries);
        formData.append('return_deliveries', returnDeliveries);
        formData.append('successful_deliveries', successfulDeliveries);
        formData.append('note', note);

        try {
            const token = localStorage.getItem('token');
            const response = await axios.post(`${baseURL}/trips/api/complete_trip/`, formData, {
                headers: { Authorization: `Token ${token}` }
            });
            console.log('Trip completed successfully:', response.data);
            navigate('/drop-off', { state: { tripId: tripId } });
        } catch (error) {
            console.error('Error completing trip:', error.response.data);
        }
    };

    return (
        <div className="container mt-5">
            <form onSubmit={handleSubmit} className="card p-4 shadow-sm">
                <h2 className="mb-4">Complete Trip</h2>
                <div className="mb-3">
                    <label className="form-label">Route No</label>
                    <input type="text" className="form-control" value={routeNo} onChange={(e) => setRouteNo(e.target.value)} required />
                </div>
                <div className="mb-3">
                    <label className="form-label">Total Deliveries</label>
                    <input type="text" className="form-control" value={totalDeliveries} onChange={(e) => setTotalDeliveries(e.target.value)} required />
                </div>
                <div className="mb-3">
                    <label className="form-label">Return Deliveries</label>
                    <input type="text" className="form-control" value={returnDeliveries} onChange={(e) => setReturnDeliveries(e.target.value)} required />
                </div>
                <div className="mb-3">
                    <label className="form-label">Successful Deliveries</label>
                    <input type="text" className="form-control" value={successfulDeliveries} onChange={(e) => setSuccessfulDeliveries(e.target.value)} required />
                </div>
                <div className="mb-3">
                    <label className="form-label">Note</label>
                    <textarea className="form-control" value={note} onChange={(e) => setNote(e.target.value)}></textarea>
                </div>
                <button type="submit" className="btn btn-primary">Complete Trip</button>
            </form>
        </div>
    );
};

export default CompleteTrip;
