import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Camera, { FACING_MODES, IMAGE_TYPES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import { QrReader } from 'react-qr-reader';
import { useGeolocated } from 'react-geolocated';
import 'bootstrap/dist/css/bootstrap.min.css';
import { baseURL } from './config';

const StartTrip = () => {
    const [carId, setCarId] = useState('');
    const [frontPhoto, setFrontPhoto] = useState(null);
    const [rearPhoto, setRearPhoto] = useState(null);
    const [leftPhoto, setLeftPhoto] = useState(null);
    const [rightPhoto, setRightPhoto] = useState(null);
    const [odometerPhoto, setOdometerPhoto] = useState(null);
    const [optionalPhoto, setOptionalPhoto] = useState(null);
    const [odometerValue, setOdometerValue] = useState('');
    const [note, setNote] = useState('');
    const [manualEntry, setManualEntry] = useState(false);
    const [step, setStep] = useState(1);
    const [currentPhotoType, setCurrentPhotoType] = useState(null);
    const [location, setLocation] = useState('');
    const [scanning, setScanning] = useState(false);
    const [isScanning, setIsScanning] = useState(false);
    const [facingMode, setFacingMode] = useState(FACING_MODES.ENVIRONMENT);
    const navigate = useNavigate();

    const { coords } = useGeolocated({
        positionOptions: {
            enableHighAccuracy: true,
        },
        userDecisionTimeout: 5000,
    });

    useEffect(() => {
        if (coords) {
            const fetchAddress = async () => {
                try {
                    const response = await axios.get(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${coords.latitude}&lon=${coords.longitude}`);
                    const address = response.data.display_name;
                    setLocation(address);
                } catch (error) {
                    console.error('Error fetching address:', error);
                }
            };

            fetchAddress();
        }
    }, [coords]);

    const handleFileChange = (setFile, dataUri) => {
        fetch(dataUri)
            .then(res => res.blob())
            .then(blob => {
                const file = new File([blob], `${currentPhotoType}.jpg`, { type: 'image/jpeg' });
                setFile(file);
            });
        setCurrentPhotoType(null);
    };

    const handleTakePhoto = (dataUri) => {
        switch (currentPhotoType) {
            case 'FrontPhoto':
                handleFileChange(setFrontPhoto, dataUri);
                break;
            case 'RearPhoto':
                handleFileChange(setRearPhoto, dataUri);
                break;
            case 'LeftPhoto':
                handleFileChange(setLeftPhoto, dataUri);
                break;
            case 'RightPhoto':
                handleFileChange(setRightPhoto, dataUri);
                break;
            case 'OdometerPhoto':
                handleFileChange(setOdometerPhoto, dataUri);
                break;
            case 'OptionalPhoto':
                handleFileChange(setOptionalPhoto, dataUri);
                break;
            default:
                break;
        }
    };

    const handleScan = (data) => {
        if (data) {
            setCarId(data);
            setIsScanning(true);
            setTimeout(() => {
                setScanning(false);
                setStep(2);
                setIsScanning(false);
            }, 1000);
        }
    };

    const handleError = (err) => {
        console.error(err);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!odometerValue || !location) {
            console.error('Pickup odometer and location are required.');
            return;
        }

        const formData = new FormData();
        formData.append('car_id', carId);
        formData.append('front_photo', frontPhoto);
        formData.append('rear_photo', rearPhoto);
        formData.append('left_photo', leftPhoto);
        formData.append('right_photo', rightPhoto);
        formData.append('odometer_photo', odometerPhoto);
        formData.append('optional_photo', optionalPhoto);
        formData.append('pick_up_odometer', odometerValue);
        formData.append('pick_up_location', location);
        formData.append('pick_up_note', note);

        try {
            const token = localStorage.getItem('token');
            const response = await axios.post(`${baseURL}/trips/api/start_trip/`, formData, {
                headers: { Authorization: `Token ${token}` },
            });
            console.log('Trip started successfully:', response.data);
            navigate('/trip-actions', { state: { tripId: response.data.trip_id } });
        } catch (error) {
            console.error('Error starting trip:', error.response.data);
        }
    };

    const changeCameraView = () => {
        setFacingMode(prevMode => prevMode === FACING_MODES.ENVIRONMENT ? FACING_MODES.USER : FACING_MODES.ENVIRONMENT);
    };

    return (
        <div className="container mt-5">
            <form onSubmit={handleSubmit} className="card p-4 shadow-sm">
                <h2 className="mb-4">Start Trip</h2>
                {step === 1 && (
                    <div>
                        <h3>Scan or Enter Car ID</h3>
                        {!manualEntry ? (
                            <>
                                <div className="mb-3">
                                    <div style={{ width: '100%', margin: '0 auto', border: isScanning ? '3px solid green' : '3px solid red' }}>
                                        {scanning ? (
                                            <QrReader
                                                delay={300}
                                                onError={handleError}
                                                onResult={handleScan}
                                                constraints={{ facingMode: 'environment' }}
                                                style={{ width: '100%' }}
                                            />
                                        ) : (
                                            <>
                                                <button type="button" className="btn btn-primary" onClick={() => setScanning(true)}>Scan QR Code</button>
                                                <button type="button" className="btn btn-secondary ms-2" onClick={() => setManualEntry(true)}>Enter Manually</button>
                                            </>
                                        )}
                                    </div>
                                </div>
                            </>
                        ) : (
                            <>
                                <div className="mb-3">
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Enter Car ID"
                                        value={carId}
                                        onChange={(e) => setCarId(e.target.value)}
                                        required
                                    />
                                </div>
                                <button type="button" className="btn btn-primary" onClick={() => setStep(2)}>Next</button>
                            </>
                        )}
                    </div>
                )}
                {step === 2 && (
                    <div>
                        <h3>Take Car Photos</h3>
                        {currentPhotoType && (
                            <div className="mb-3">
                                <Camera
                                    onTakePhoto={(dataUri) => handleTakePhoto(dataUri)}
                                    idealFacingMode={facingMode}
                                    isImageMirror={false}
                                    isFullscreen={false}
                                />
                                <div className="mt-2">
                                    <button className="btn btn-secondary" onClick={() => setCurrentPhotoType(null)}>Cancel</button>
                                </div>
                            </div>
                        )}
                        {!currentPhotoType && (
                            <div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('FrontPhoto')}>
                                        Take Front Photo
                                    </button>
                                    {frontPhoto && <img src={URL.createObjectURL(frontPhoto)} alt="Front" className="img-thumbnail" width="100" />}
                                </div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('RearPhoto')}>
                                        Take Rear Photo
                                    </button>
                                    {rearPhoto && <img src={URL.createObjectURL(rearPhoto)} alt="Rear" className="img-thumbnail" width="100" />}
                                </div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('LeftPhoto')}>
                                        Take Left Photo
                                    </button>
                                    {leftPhoto && <img src={URL.createObjectURL(leftPhoto)} alt="Left" className="img-thumbnail" width="100" />}
                                </div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('RightPhoto')}>
                                        Take Right Photo
                                    </button>
                                    {rightPhoto && <img src={URL.createObjectURL(rightPhoto)} alt="Right" className="img-thumbnail" width="100" />}
                                </div>
                                <button type="button" className="btn btn-primary" onClick={() => setStep(3)}>Next</button>
                            </div>
                        )}
                    </div>
                )}
                {step === 3 && (
                    <div>
                        <h3>Enter Odometer Details</h3>
                        <div className="mb-3">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Odometer Value"
                                value={odometerValue}
                                onChange={(e) => setOdometerValue(e.target.value)}
                                required
                            />
                        </div>
                        {currentPhotoType && (
                            <div className="mb-3">
                                <Camera
                                    onTakePhoto={(dataUri) => handleTakePhoto(dataUri)}
                                    idealFacingMode={facingMode}
                                    isImageMirror={false}
                                    isFullscreen={false}
                                />
                                <div className="mt-2">
                                    <button className="btn btn-secondary" onClick={() => setCurrentPhotoType(null)}>Cancel</button>
                                </div>
                            </div>
                        )}
                        {!currentPhotoType && (
                            <div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('OdometerPhoto')}>
                                        Take Odometer Photo
                                    </button>
                                    {odometerPhoto && <img src={URL.createObjectURL(odometerPhoto)} alt="Odometer" className="img-thumbnail" width="100" />}
                                </div>
                                <button type="button" className="btn btn-primary" onClick={() => setStep(4)}>Next</button>
                            </div>
                        )}
                    </div>
                )}
                {step === 4 && (
                    <div>
                        <h3>Optional Details</h3>
                        <div className="mb-3">
                            <textarea
                                className="form-control"
                                placeholder="Pick up note"
                                value={note}
                                onChange={(e) => setNote(e.target.value)}
                            ></textarea>
                        </div>
                        {currentPhotoType && (
                            <div className="mb-3">
                                <Camera
                                    onTakePhoto={(dataUri) => handleTakePhoto(dataUri)}
                                    idealFacingMode={facingMode}
                                    isImageMirror={false}
                                    isFullscreen={false}
                                />
                                <div className="mt-2">
                                    <button className="btn btn-secondary" onClick={() => setCurrentPhotoType(null)}>Cancel</button>
                                </div>
                            </div>
                        )}
                        {!currentPhotoType && (
                            <div>
                                <div className="mb-3">
                                    <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('OptionalPhoto')}>
                                        Take Optional Photo
                                    </button>
                                    {optionalPhoto && <img src={URL.createObjectURL(optionalPhoto)} alt="Optional" className="img-thumbnail" width="100" />}
                                </div>
                                <button type="submit" className="btn btn-primary">Start Trip</button>
                            </div>
                        )}
                    </div>
                )}
            </form>
        </div>
    );
};

export default StartTrip;
