import React, { useContext, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { AuthProvider } from './context/AuthContext';
import AuthContext from './context/AuthContext';
import Login from './components/Login';
import Register from './components/Register';
import DriverDashboard from './components/DriverDashboard';
import CarOwnerDashboard from './components/CarOwnerDashboard';
import ManageCars from './components/ManageCars';
import AdminDashboard from './components/AdminDashboard';
import Profile from './components/Profile';
import StartTrip from './components/StartTrip';
import AddFuel from './components/AddFuel';
import CompleteTrip from './components/CompleteTrip';
import DropOff from './components/DropOff';
import PreviousTrips from './components/PreviousTrips';
import TripActions from './components/TripActions';

import Header from './context/Header';

import { requestPermissions, WebcamComponent, GeolocationComponent, QrReaderComponent } from './context/Permissions';

const App = () => {
    return (
        <AuthProvider>
            <Router>
                <Header />
                <Routes>
                    <Route path="/login" element={<Login />} />
                    <Route path="/register" element={<Register />} />
                    <Route path="/profile" element={<PrivateRoute component={Profile} />} />
                    <Route path="/dashboard" element={<PrivateRoute component={Dashboard} />} />
                    <Route path="/start-trip" element={<PrivateRoute component={StartTrip} />} />
                    <Route path="/trip-actions" element={<PrivateRoute component={TripActions} />} />
                    <Route path="/manage-cars" element={<PrivateRoute component={ManageCars} />} />
                    <Route path="/add-fuel" element={<PrivateRoute component={AddFuel} />} />
                    <Route path="/complete-trip" element={<PrivateRoute component={CompleteTrip} />} />
                    <Route path="/drop-off" element={<PrivateRoute component={DropOff} />} />
                    <Route path="/previous-trips" element={<PrivateRoute component={PreviousTrips} />} />
                    <Route path="*" element={<Navigate to="/login" />} />
                </Routes>
            </Router>
        </AuthProvider>
    );
};

const PrivateRoute = ({ component: Component }) => {
    const { user } = useContext(AuthContext);
    useEffect(() => {
        console.log("PrivateRoute user:", user);
    }, [user]);
    return user ? <Component /> : <Navigate to="/login" />;
};

const Dashboard = () => {
    const { user } = useContext(AuthContext);
    useEffect(() => {
        console.log("Dashboard user:", user);
    }, [user]);
    if (!user) {
        return <Navigate to="/login" />;
    }
    switch (user.role) {
        case 'driver':
            return <DriverDashboard />;
        case 'renter':
            return <CarOwnerDashboard />;
        case 'admin':
            return <AdminDashboard />;
        default:
            return <Navigate to="/login" />;
    }
};

export default App;
