import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import { baseURL } from './config';

const DriverDashboard = () => {
    const [ongoingTrip, setOngoingTrip] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        const checkOngoingTrip = async () => {
            const token = localStorage.getItem('token');
            try {
                const response = await axios.get(`${baseURL}/trips/api/check_ongoing_trip/`, {
                    headers: { Authorization: `Token ${token}` }
                });
                if (response.data.status === 'in_progress') {
                    setOngoingTrip(response.data.trip_id);
                } else {
                    setOngoingTrip(null);
                }
            } catch (error) {
                console.error('Error checking ongoing trip:', error);
            }
        };

        checkOngoingTrip();
    }, []);

    const handleStartTrip = () => {
        navigate('/start-trip');
    };

    const handleTripActions = () => {
        navigate('/trip-actions', { state: { tripId: ongoingTrip } });
    };

    const handleProfile = () => {
        navigate('/profile');
    };

    const handlePreviousTrips = () => {
        navigate('/previous-trips');
    };

    return (
        <div className="container mt-5">
            <div className="card p-4 shadow-sm">
                <h2 className="mb-4 text-center">Driver Dashboard</h2>
                <ul className="list-group">
                    <li className="list-group-item d-flex justify-content-between align-items-center">
                        <span>Profile</span>
                        <button className="btn btn-primary" onClick={handleProfile}>Go</button>
                    </li>
                    {ongoingTrip ? (
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            <span>Trip Actions</span>
                            <button className="btn btn-warning" onClick={handleTripActions}>Go</button>
                        </li>
                    ) : (
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            <span>Start Trip</span>
                            <button className="btn btn-success" onClick={handleStartTrip}>Go</button>
                        </li>
                    )}
                    <li className="list-group-item d-flex justify-content-between align-items-center">
                        <span>Previous Trips</span>
                        <button className="btn btn-info" onClick={handlePreviousTrips}>Go</button>
                    </li>
                </ul>
            </div>
        </div>
    );
};

export default DriverDashboard;
