import React, { useState, useEffect } from 'react';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import { baseURL } from './config';
import { Typeahead } from 'react-bootstrap-typeahead';
import 'react-bootstrap-typeahead/css/Typeahead.css';
import './PreviousTrips.css'; // Create this CSS file for custom styling

const PreviousTrips = () => {
    const [trips, setTrips] = useState([]);
    const [error, setError] = useState('');
    const [expandedTrip, setExpandedTrip] = useState(null);
    const [modalImage, setModalImage] = useState(null);
    const [filters, setFilters] = useState({
        pickupDate: '',
        driverName: '',
        licensePlate: '',
        status: 'all'  // new filter for trip status
    });
    const [driverSuggestions, setDriverSuggestions] = useState([]);
    const [licenseSuggestions, setLicenseSuggestions] = useState([]);

    useEffect(() => {
        const fetchTrips = async () => {
            try {
                const token = localStorage.getItem('token');
                const response = await axios.get(`${baseURL}/profiles/api/trips/`, {
                    headers: { Authorization: `Token ${token}` }
                });
                setTrips(response.data);
                setDriverSuggestions([...new Set(response.data.map(trip => trip.driver.username))]);
                setLicenseSuggestions([...new Set(response.data.map(trip => trip.car.license_plate))]);
            } catch (err) {
                setError('Error fetching trips');
            }
        };

        fetchTrips();
    }, []);

    const handleSeeMore = (tripId) => {
        setExpandedTrip(expandedTrip === tripId ? null : tripId);
    };

    const handleImageClick = (url) => {
        setModalImage(url);
    };

    const closeModal = () => {
        setModalImage(null);
    };

    const handleChange = (event) => {
        const { name, value } = event.target;
        setFilters(prevFilters => ({
            ...prevFilters,
            [name]: value
        }));
    };

    const handleDriverChange = (selected) => {
        setFilters(prevFilters => ({
            ...prevFilters,
            driverName: selected.length > 0 ? selected[0] : ''
        }));
    };

    const handleLicenseChange = (selected) => {
        setFilters(prevFilters => ({
            ...prevFilters,
            licensePlate: selected.length > 0 ? selected[0] : ''
        }));
    };

    const filteredTrips = trips.filter(trip => {
        return (filters.pickupDate ? new Date(trip.pick_up_time).toLocaleDateString() === new Date(filters.pickupDate).toLocaleDateString() : true)
            && (filters.driverName ? trip.driver.username.toLowerCase().includes(filters.driverName.toLowerCase()) : true)
            && (filters.licensePlate ? trip.car.license_plate.toLowerCase().includes(filters.licensePlate.toLowerCase()) : true)
            && (filters.status === 'all' ? true : trip.status === filters.status);
    });

    return (
        <div className="container mt-5">
            <h2 className="mb-4">Previous Trips</h2>
            {error && <div className="alert alert-danger">{error}</div>}
            <div className="mb-4">
                <div className="row">
                    <div className="col-md-3">
                        <input type="date" name="pickupDate" className="form-control" value={filters.pickupDate} onChange={handleChange} placeholder="Filter by Pickup Date" />
                    </div>
                    <div className="col-md-3">
                        <Typeahead
                            id="driverName"
                            onChange={handleDriverChange}
                            options={driverSuggestions}
                            placeholder="Filter by Driver Name"
                            selected={filters.driverName ? [filters.driverName] : []}
                            className="typeahead"
                        />
                    </div>
                    <div className="col-md-3">
                        <Typeahead
                            id="licensePlate"
                            onChange={handleLicenseChange}
                            options={licenseSuggestions}
                            placeholder="Filter by Car Rego Number"
                            selected={filters.licensePlate ? [filters.licensePlate] : []}
                            className="typeahead"
                        />
                    </div>
                    <div className="col-md-3">
                        <select name="status" className="form-control" value={filters.status} onChange={handleChange}>
                            <option value="all">All Trips</option>
                            <option value="pending">Pending Trips</option>
                            <option value="in_progress">In Progress Trips</option>
                            <option value="completed">Completed Trips</option>
                        </select>
                    </div>
                </div>
            </div>
            {trips.length === 0 ? (
                <div className="alert alert-info">No previous trips found.</div>
            ) : (
                <ul className="list-group">
                    {filteredTrips.map((trip) => (
                        <li key={trip.trip_id} className="list-group-item mb-3">
                            <div className="row">
                                <div className="col-12">
                                    <div className="d-flex justify-content-between align-items-center">
                                        <div>
                                            <p><strong>Driver:</strong> {trip.driver.username}</p>
                                            <p><strong>Route No:</strong> {trip.trip_details.route_no}</p>
                                            <p><strong>Car Rego:</strong> {trip.car.license_plate}</p>
                                            <p><strong>Pick Up Time:</strong> {new Date(trip.pick_up_time).toLocaleString()}</p>
                                            <p><strong>Drop Off Time:</strong> {trip.drop_off_time ? new Date(trip.drop_off_time).toLocaleString() : 'N/A'}</p>
                                        </div>
                                        <button className="btn btn-primary" onClick={() => handleSeeMore(trip.trip_id)}>
                                            {expandedTrip === trip.trip_id ? 'Show Less' : 'See More'}
                                        </button>
                                    </div>
                                    <div className={`status-box ${trip.status}`}>
                                        <p><strong>Status:</strong> {trip.status.replace('_', ' ').toUpperCase()}</p>
                                    </div>
                                </div>
                            </div>
                            {expandedTrip === trip.trip_id && (
                                <div className="mt-3">
                                    <p><strong>Pick Up Location:</strong> {trip.pick_up_location}</p>
                                    <p><strong>Drop Off Location:</strong> {trip.drop_off_location || 'N/A'}</p>
                                    <p><strong>Pick Up Odometer:</strong> {trip.pick_up_odometer}</p>
                                    <p><strong>Drop Off Odometer:</strong> {trip.drop_off_odometer || 'N/A'}</p>
                                    <p><strong>Pick Up Note:</strong> {trip.pick_up_note || 'N/A'}</p>
                                    <p><strong>Drop Off Note:</strong> {trip.drop_off_note || 'N/A'}</p>
                                    <p><strong>Fuel Details:</strong></p>
                                    {trip.fuel_details && trip.fuel_details.length > 0 && (
                                        <ul className="list-unstyled">
                                            {trip.fuel_details.map((fuel, index) => (
                                                <li key={index} className="mb-3">
                                                    <p><strong>Amount:</strong> {fuel.fuel_amount}</p>
                                                    <p><strong>Note:</strong> {fuel.note}</p>
                                                    <img
                                                        src={fuel.fuel_receipt_photo}
                                                        alt="Fuel receipt"
                                                        className="img-thumbnail"
                                                        style={{ width: '100px', height: '100px', cursor: 'pointer' }}
                                                        onClick={() => handleImageClick(fuel.fuel_receipt_photo)}
                                                        onError={(e) => { e.target.onerror = null; e.target.src = 'default-image.png'; }} // Fallback to a default image if the image fails to load
                                                    />
                                                </li>
                                            ))}
                                        </ul>
                                    )}
                                    <div>
                                        <p><strong>Pick Up Photos:</strong></p>
                                        <div className="d-flex flex-wrap">
                                            {trip.pick_up_photos && Object.entries(trip.pick_up_photos).map(([key, url]) => (
                                                <img
                                                    key={key}
                                                    src={url}
                                                    alt={`${key} photo`}
                                                    className="img-thumbnail m-2"
                                                    style={{ width: '100px', height: '100px', cursor: 'pointer' }}
                                                    onClick={() => handleImageClick(url)}
                                                    onError={(e) => { e.target.onerror = null; e.target.src = 'default-image.png'; }} // Fallback to a default image if the image fails to load
                                                />
                                            ))}
                                        </div>
                                    </div>
                                    <div>
                                        <p><strong>Drop Off Photos:</strong></p>
                                        <div className="d-flex flex-wrap">
                                            {trip.drop_off_photos && trip.drop_off_photos !== null && Object.entries(trip.drop_off_photos).map(([key, url]) => (
                                                <img
                                                    key={key}
                                                    src={url}
                                                    alt={`${key} photo`}
                                                    className="img-thumbnail m-2"
                                                    style={{ width: '100px', height: '100px', cursor: 'pointer' }}
                                                    onClick={() => handleImageClick(url)}
                                                    onError={(e) => { e.target.onerror = null; e.target.src = 'default-image.png'; }} // Fallback to a default image if the image fails to load
                                                />
                                            ))}
                                        </div>
                                    </div>
                                    {trip.trip_details && (
                                        <>
                                            <p><strong>Trip Details:</strong></p>
                                            <ul className="list-unstyled">
                                                <li><strong>Route No:</strong> {trip.trip_details.route_no}</li>
                                                <li><strong>Total Deliveries:</strong> {trip.trip_details.total_deliveries}</li>
                                                <li><strong>Return Deliveries:</strong> {trip.trip_details.return_deliveries}</li>
                                                <li><strong>Successful Deliveries:</strong> {trip.trip_details.successful_deliveries}</li>
                                                <li><strong>Note:</strong> {trip.trip_details.note}</li>
                                            </ul>
                                        </>
                                    )}
                                </div>
                            )}
                        </li>
                    ))}
                </ul>
            )}

            {modalImage && (
                <div className="modal" style={modalStyle}>
                    <span className="close" style={closeStyle} onClick={closeModal}>&times;</span>
                    <img className="modal-content" src={modalImage} style={modalImageStyle} />
                </div>
            )}
        </div>
    );
};

const modalStyle = {
    display: 'block',
    position: 'fixed',
    zIndex: 1,
    paddingTop: '100px',
    left: 0,
    top: 0,
    width: '100%',
    height: '100%',
    overflow: 'auto',
    backgroundColor: 'rgb(0,0,0)',
    backgroundColor: 'rgba(0,0,0,0.9)'
};

const closeStyle = {
    position: 'absolute',
    top: '15px',
    right: '35px',
    color: '#f1f1f1',
    fontSize: '40px',
    fontWeight: 'bold',
    transition: '0.3s',
    cursor: 'pointer'
};

const modalImageStyle = {
    margin: 'auto',
    display: 'block',
    width: '80%',
    maxWidth: '700px'
};

export default PreviousTrips;
