import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate, useLocation } from 'react-router-dom';
import Camera, { FACING_MODES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { baseURL } from './config';

const AddFuel = () => {
    const [fuelAmount, setFuelAmount] = useState('');
    const [fuelReceiptPhoto, setFuelReceiptPhoto] = useState(null);
    const [note, setNote] = useState('');
    const [currentPhotoType, setCurrentPhotoType] = useState(null);
    const [facingMode, setFacingMode] = useState(FACING_MODES.ENVIRONMENT);
    const navigate = useNavigate();
    const location = useLocation();
    const tripId = location.state?.tripId;

    const handleFileChange = (setFile, dataUri) => {
        fetch(dataUri)
            .then(res => res.blob())
            .then(blob => {
                const file = new File([blob], `${currentPhotoType}.jpg`, { type: 'image/jpeg' });
                setFile(file);
            });
        setCurrentPhotoType(null);
    };

    const handleTakePhoto = (dataUri) => {
        if (currentPhotoType === 'FuelReceiptPhoto') {
            handleFileChange(setFuelReceiptPhoto, dataUri);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('trip_id', tripId);
        formData.append('fuel_amount', fuelAmount);
        formData.append('fuel_receipt_photo', fuelReceiptPhoto);
        formData.append('note', note);

        try {
            const token = localStorage.getItem('token');
            const response = await axios.post(`${baseURL}/trips/api/add_fuel/`, formData, {
                headers: { Authorization: `Token ${token}` },
            });
            console.log('Fuel added successfully:', response.data);
            navigate('/trip-actions', { state: { tripId: tripId } });
        } catch (error) {
            console.error('Error adding fuel:', error.response.data);
        }
    };

    const changeCameraView = () => {
        setFacingMode(prevMode => prevMode === FACING_MODES.ENVIRONMENT ? FACING_MODES.USER : FACING_MODES.ENVIRONMENT);
    };

    return (
        <div className="container mt-5">
            <form onSubmit={handleSubmit} className="card p-4 shadow-sm">
                <h2 className="mb-4">Add Fuel</h2>
                <div className="mb-3">
                    <label className="form-label">Fuel Amount</label>
                    <input type="text" className="form-control" value={fuelAmount} onChange={(e) => setFuelAmount(e.target.value)} required />
                </div>
                <div className="mb-3">
                    <label className="form-label">Fuel Receipt Photo</label>
                    {currentPhotoType && (
                        <div className="mb-3">
                            <Camera
                                onTakePhoto={(dataUri) => handleTakePhoto(dataUri)}
                                idealFacingMode={facingMode}
                                isImageMirror={false}
                                isFullscreen={false}
                            />
                            <div className="mt-2">
                                <button className="btn btn-secondary" onClick={() => setCurrentPhotoType(null)}>Cancel</button>
                            </div>
                        </div>
                    )}
                    {!currentPhotoType && (
                        <>
                            <button type="button" className="btn btn-outline-primary me-2" onClick={() => setCurrentPhotoType('FuelReceiptPhoto')}>
                                Take Fuel Receipt Photo
                            </button>
                            {fuelReceiptPhoto && <img src={URL.createObjectURL(fuelReceiptPhoto)} alt="Fuel Receipt" className="img-thumbnail" width="100" />}
                        </>
                    )}
                </div>
                <div className="mb-3">
                    <label className="form-label">Note</label>
                    <textarea className="form-control" value={note} onChange={(e) => setNote(e.target.value)}></textarea>
                </div>
                <button type="submit" className="btn btn-primary">Add Fuel</button>
            </form>
        </div>
    );
};

export default AddFuel;
