import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Alert, Button, ListGroup, Container } from 'react-bootstrap';
import { baseURL } from './config';

const ManageDrivers = () => {
    const [drivers, setDrivers] = useState([]);
    const [error, setError] = useState('');

    useEffect(() => {
        const fetchDrivers = async () => {
            try {
                const token = localStorage.getItem('token');
                const response = await axios.get(`${baseURL}/profiles/api/drivers/`, {
                    headers: { Authorization: `Token ${token}` }
                });
                setDrivers(response.data.filter(user => user.role === 'driver'));
            } catch (err) {
                setError('Error fetching drivers');
            }
        };

        fetchDrivers();
    }, []);

    const handleRemoveDriver = async (driverId) => {
        try {
            const token = localStorage.getItem('token');
            await axios.delete(`${baseURL}/profiles/api/profile/${driverId}/`, {
                headers: { Authorization: `Token ${token}` }
            });
            setDrivers(drivers.filter(driver => driver.id !== driverId));
        } catch (err) {
            setError('Error removing driver');
        }
    };

    return (
        <Container className="mt-5">
            <h3>Manage Drivers</h3>
            {error && <Alert variant="danger">{error}</Alert>}
            <ListGroup>
                {drivers.map(driver => (
                    <ListGroup.Item key={driver.id} className="d-flex justify-content-between align-items-center">
                        <div>
                            <strong>{driver.username}</strong> - {driver.email}
                        </div>
                        <Button variant="danger" size="sm" onClick={() => handleRemoveDriver(driver.id)}>
                            Remove
                        </Button>
                    </ListGroup.Item>
                ))}
            </ListGroup>
        </Container>
    );
};

export default ManageDrivers;
