import React, { useState, useRef } from 'react';
import QRCode from 'qrcode.react';
import html2canvas from 'html2canvas';
import 'bootstrap/dist/css/bootstrap.min.css';
import { baseURL } from './config';

const CarDetails = ({ car }) => {
    const [showQRCode, setShowQRCode] = useState(false);
    const qrRef = useRef(null);
    const printRef = useRef();

    if (!car) return null;

    const handleGenerateQRCode = () => {
        setShowQRCode(true);
    };

    const handleDownloadQRCode = async () => {
        const canvas = await html2canvas(qrRef.current);
        const imgData = canvas.toDataURL('image/png');
        const link = document.createElement('a');
        link.href = imgData;
        link.download = String(car.license_plate) + '_qr_code.png';
        link.click();
    };

    return (
        <div className="container mt-4">
            <div className="card shadow-sm p-4" ref={printRef}>
                <h3 className="card-title">Car Details</h3>
                <div className="card-body">
                    <p className="card-text"><strong>ID:</strong> {car.car_id}</p>
                    <p className="card-text"><strong>Model:</strong> {car.model}</p>
                    <p className="card-text"><strong>License Plate:</strong> {car.license_plate}</p>
                    <p className="card-text"><strong>Available:</strong> {car.available ? 'Yes' : 'No'}</p>
                    <button className="btn btn-primary mt-3" onClick={handleGenerateQRCode}>
                        Generate QR Code
                    </button>
                    {showQRCode && (
                        <div className="mt-3">
                            <div ref={qrRef}>
                                <QRCode value={String(car.car_id)} size={256} />
                            </div>
                            <button className="btn btn-secondary mt-3" onClick={handleDownloadQRCode}>
                                Download QR Code
                            </button>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default CarDetails;
